.content{
    display: flex;
    flex-direction: column;
    align-items: center;

}
.container {

    display: flex;
    justify-content: center;

}
.tableClass {
    background: aqua;
    width: 200px;
    height: 50px;
    border-radius: 5px;
}
.inputClass {

    width: 200px;
    height: 45px;
    background: dimgray;
    color: aliceblue;
    font-size: 50px;
}
.selectClass {
    width: 40px;
    height: 40px;
    font-size: 30px;
    background: dimgray;
    color: aliceblue;
}
.buttonClass {
    background: #3c393c;
    color: aliceblue;
    font-size: 35px;
    width: 200px;
    height: 100px;
    transform: translate(0px, 8px);
}
::placeholder {
    color: #aab2a9;
}
.form_input{
    border-color: aqua; border-width: 6px;
    width: 200px;
    height: 27px;
    background: dimgray;
    color: aliceblue;
    font-size: 15px;
    transform: translate(0px, 2px);
    margin-top: -2px;

}
.form_button{

    width: 200px;
    height: 40px;
    background: #3c393c;
    color: aliceblue;
    font-size: 15px;

}


@media screen and (max-width: 460px){
    .content{
        display: flex;
        flex-direction: column;
        align-items: center;

    }
    .container {

        display: flex;
        justify-content: center;

    }
    .tableClass {
        background: aqua;
        width: 345px;
        height: 50px;
        border-radius: 5px;
    }
    .inputClass {

        width: 110px;
        height: 30px;
        background: dimgray;
        color: aliceblue;
        font-size: 20px;
    }
    .selectClass {
        width: 40px;
        height: 25px;
        font-size: 10px;
        background: dimgray;
        color: aliceblue;
    }
    .buttonClass {
        background: #3c393c;
        color: aliceblue;
        font-size: 20px;
        width: 110px;
        height: 50px;
        transform: translate(0px, 8px);
    }
    ::placeholder {
        color: #aab2a9;
    }
    .form_input{
        border-color: aqua; border-width: 3px;
        width: 110px;
        height: 20px;
        background: dimgray;
        color: aliceblue;
        font-size: 12px;
        transform: translate(0px, 2px);
        margin-top: -2px;

    }
    .form_button{

        width: 110px;
        height: 26px;
        background: #3c393c;
        color: aliceblue;
        font-size: 8px;

    }

}



